.searchPage{
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 10;
    background: #fff;
    width: 100%;
    .header{
        min-height: 44px;
        padding: 10px 0;
       .top{
         position: relative;
         padding: 0 20px;
         #keyList{
             position: absolute;
             width: 88%;
             background: #fefefe;
             li{
                 padding: 10px 5px;
             }
             li:hover{
                 background: #999;
                 color: #fff;
             }
         }
         @mixin icon {
            position: absolute;
            font-size: 30px;
            color: #999;
         }
        .fdj{
            @include icon;
            left: 30px;
            top: 5px;
        }
        .sys{
            @include icon;
            font-size: 25px;
            bottom: 0;
            right: 10px;
        }
        .cancel{
            position: absolute;
            right: 5px;
            color: #555;
            font-size: 18px;
            top: 50%;
            transform: translateY(-50%);
        }
        .search{
            border: 0.5px solid #ddd;
            height: 30px;
            width: 92%;
            border-radius: 30px;
            padding: 0 10px 0 50px;
        }
       }
    }
    .navbar{
        height: 48px;
        line-height: 48px;
        display: flex;
        text-align: center;
        span{
            flex:1;
            color: #555;
            position: relative;
            background: #fff;
            &.active{
                font-size: 18px; 
                color: #000;
                font-weight: bold;
            }
            &.active::after{
                position: absolute;
                content:'';
                bottom: 0px;
                width: 40%;
                height: 3px;
                background: crimson;
                border-radius: 2px;
                left: 50%;
                transform: translateX(-50%);
            }
        }
    }
    .appoint{
        margin-top: 20px;
        h3{
            text-align: center;
            padding: 10px 0;
            color: #999;
        }
        p{
           display: flex;
           span{
             flex: 1;
             text-align: center;
             padding: 10px 0;
          } 
        }
        
    }
    .myhistory{
        padding: 0 20px;
        h4{color: #999;}
        .tabs{
            padding: 2px 5px;
            margin-right: 5px;
            margin-top: 10px;
            display: inline-block;
            font-size: 12px;
            border: 0.5px solid #999;
            border-radius: 5px;
        }
    }
    .searchResult{
        position: absolute;
        width: 100%;
        top: 50px;
        left: 0;
        bottom: 0;
        background: #fff;
    }
    .course{
        padding:0 10px;
        .item{
            position: relative;
            padding: 10px 0;
            display: flex;
            img{
                width: 90px;
                height: 87px;
                border-radius: 5px;
                background: lightblue;
            }
            .right{
                flex: 1;
                padding: 0 5px;
                display: flex;
                flex-direction: column;
                justify-content: space-around;
                div{
                    display: flex;
                    justify-content: space-between;
                    p{max-width: 100px;}
                }
            }
            p:nth-of-type(3){
                text-align: right;
                span{margin-left: 5px;}
            }
        }
    }
    .tab{
        padding: 2px 5px;
        border-radius: 5px;
        background: #e6750b;
        color: #fff;
        font-size: 14px;
    }
    .tab.self{
        position: absolute;
        right: 10px;
        top: 5px;
        background: #24ac09;
    }
    .question{
        p{  
            padding: 10px;
            display: flex;
        }
        span:nth-of-type(2){
            flex: 1;
            padding:0 5px;
            font-size: 18px;
        }
        span:nth-of-type(3){
            font-size: 20px;
            width: 20px;
        }
    }
}
#questionDetail{
    bottom: 0;
    z-index: 1000 !important;
    img{width: 100%;height: 290px;}
    .item{
        display: flex;
        padding:10px;
        justify-content: space-around;
        justify-items: center;
    }
    .next{
        background: #48c232;
        color: #fff;
        width: 60%;
        text-align: center;
        font-size: 20px;
        padding: 10px 0;
        margin: 20px auto;
        border-radius: 10px;
    }
    .showBox{
        color: #fff;
        h3{
            padding-bottom: 10px;
        }
        h4{line-height: 30px;}
    }
    input{
       height:20px;
       width:18px;
    }
    label{
        font-size:20px;
        flex:1;
    }
 }   